@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Segoe+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700&display=swap');

.font-playfair { font-family: 'Playfair Display', serif; }
.font-lato { font-family: 'Lato', sans-serif; }
.font-raleway { font-family: 'Raleway', sans-serif; }
.font-segoe-script { font-family: 'Segoe Script', cursive; }
.font-segoe-ui { font-family: 'Segoe UI', system-ui, -apple-system, sans-serif; }
.font-inter { font-family: 'Inter', system-ui, sans-serif; }
.font-plus-jakarta-sans { font-family: 'Plus Jakarta Sans', sans-serif; }

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px transparent inset !important;
  transition: background-color 5000s ease-in-out 0s;
}

:root {
    background: #F9EEDD;
    --color-primary-color: #FF752B;
    --color-primary-text: #362925;
    font-family: 'Lato', sans-serif;
}

button {
  cursor: pointer;
}

input { 
  background-color: #0000;
}

::placeholder {
  color: #718096;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-right: 16px;
  padding-left: 16px;
}

.header {
  .menu-link {
    font-size: 24px;
    font-weight: 600;
    cursor: pointer;
    white-space: nowrap;
    color: #000;
    transition: all 0.3s;
    &:hover {
      color: var(--color-primary-color);
    }
  }
}

.title {
  font-family: 'Playfair Display', serif;
  font-weight: 500;
}


.title-segeo {
  font-family: 'Segoe Script', cursive;
  font-weight: 500;
}

.text {
  font-family: 'Lato', sans-serif;
  color: var(--color-primary-text);
  span, a {
      color: var(--color-primary-color);
  }
  a {
    &:hover {
      text-decoration: underline;
    }
  }
}

.welcome-title {
  line-height: 110%;
  letter-spacing: 0%;
  span {
    color: var(--color-primary-color);
  }
}

.welcome-text {
  font-weight: 400;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
}

.chakra-row {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0px;
  margin: 40px 0;
  overflow: visible;
}

.chakra-row-smallest {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0px;
  margin: 5px 0;
  overflow: visible;
}

.big-chakra-circle {
  width: 80%;
  height: 80%;
  border-radius: 50%;
  filter: blur(10px);
  opacity: 0.85;
  animation: float 2.5s ease-in-out infinite;
  z-index: 1;
}

.chakra-circle {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  filter: blur(10px);
  opacity: 0.85;
  animation: float 2.5s ease-in-out infinite;
  z-index: 1;
}

.chakra-small-circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  filter: blur(10px);
  opacity: 0.85;
  animation: float 2.5s ease-in-out infinite;
  z-index: 1;
}

.chakra-smallest-circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  filter: blur(1px);
  opacity: 0.85;
  animation: float_small 2.5s ease-in-out infinite;
  z-index: 1;
}

.purple { background: #a259ff; }
.indigo { background: #5b6ee1; }
.blue   { background: #4fc3f7; }
.green  { background: #43a047; }
.yellow { background: #ffe066; }
.orange { background: #ff9800; }
.red    { background: #f44336; }

.chakra-circle:nth-child(2) { animation-delay: 0.2s; }
.chakra-circle:nth-child(3) { animation-delay: 0.4s; }
.chakra-circle:nth-child(4) { animation-delay: 0.6s; }
.chakra-circle:nth-child(5) { animation-delay: 0.8s; }
.chakra-circle:nth-child(6) { animation-delay: 1s; }
.chakra-circle:nth-child(7) { animation-delay: 1.2s; }

.chakra-small-circle:nth-child(2) { animation-delay: 0.2s; }
.chakra-small-circle:nth-child(3) { animation-delay: 0.4s; }
.chakra-small-circle:nth-child(4) { animation-delay: 0.6s; }
.chakra-small-circle:nth-child(5) { animation-delay: 0.8s; }
.chakra-small-circle:nth-child(6) { animation-delay: 1s; }
.chakra-small-circle:nth-child(7) { animation-delay: 1.2s; }

.chakra-smallest-circle:nth-child(2) { animation-delay: 0.2s; }
.chakra-smallest-circle:nth-child(3) { animation-delay: 0.4s; }
.chakra-smallest-circle:nth-child(4) { animation-delay: 0.6s; }
.chakra-smallest-circle:nth-child(5) { animation-delay: 0.8s; }
.chakra-smallest-circle:nth-child(6) { animation-delay: 1s; }
.chakra-smallest-circle:nth-child(7) { animation-delay: 1.2s; }

.sparkle-image {
  width: 120px;
  height: 120px;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50%      { transform: translateY(-10px); }
}

@keyframes float_small {
  0%, 100% { transform: translateY(0); }
  50%      { transform: translateY(-3px); }
}

@media (max-width: 1024px) {
  .chakra-row {
    margin: 32px 0;
  }
  .chakra-circle {
    width: 44px;
    height: 44px;
    filter: blur(7px);
  }

  .sparkle-image {
    width: 100px;
    height: 100px;
  }
}

@media (max-width: 640px) {
  .chakra-row {
    margin: 20px 0;
  }
  .chakra-circle {
    width: 28px;
    height: 28px;
    filter: blur(4px);
  }
  
  .sparkle-image {
    width: 80px;
    height: 80px;
  }
}

.intersect {
  background-image: url('../public/assets/intersect.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.sign-desktop-background {
  background-image: url('../public/assets/sign_bg.png');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.sign-mobile-background {
  background-image: url('../public/assets/sign_ip_bg.png');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.zen-card-title {
  font-family: Playfair;
  font-weight: 500;
  font-size: 40px;
  line-height: 35px;
  text-align: center;
}

.zen-coming-card-title {
  font-family: Playfair;
  font-weight: 800;
  font-size: 50px;
  line-height: 55px;
  text-align: center;
  padding: 20px 0
}

// Custom style for small chakra dots with blur
.chakra-dot-sm {
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  filter: blur(2px); // Apply blur here
}

.fade-in {
  animation: fadeIn 2s ease-out;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(20px); }
  100% { opacity: 1; transform: translateY(0); }
}

@keyframes rotate-clockwise {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(-360deg); }
}

.collaborator-text {
  line-height: 50px !important;
}

@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(191,161,74,0.2);
  }
  50% {
    box-shadow: 0 0 20px rgba(191,161,74,0.4);
  }
}

.animate-glow {
  animation: glow 2s ease-in-out infinite;
}

.chakra-load-shape {
  position: absolute;
  width: 400px;
  height: 400px;
}

@keyframes float {
  0% {
      transform: translateY(0px);
  }
  50% {
      transform: translateY(-20px);
  }
  100% {
      transform: translateY(0px);
  }
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

@keyframes chakra-opacity {
  0% {
      opacity: 0.4;
  }
  50% {
      opacity: 0.8;
  }
  100% {
      opacity: 0.4;
  }
}

.animate-opacity {
  animation: chakra-opacity 3s ease-in-out infinite;
}


.promembership {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 30px;
    background-image: linear-gradient(#FFCD29, #FAD859, #FFCD29);
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
  }
}

.quad-gradient {
  background-image: linear-gradient(to right, #A823F9, #7912A9, #7912A9, #FFFFFF);
}

.explosion-dot {
  border-radius: 50%;
  position: absolute;
  pointer-events: none;
  z-index: 1000;
}
